import { Route } from '@angular/compiler/src/core';
import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import { Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';

import { ActionLabel } from "@enum/app_enum";
import { SupportService } from "@core/services/support.service";
@Component({
  selector: 'app-dynamic-table',
  templateUrl: './dynamic-table.component.html',
  styleUrls: ['./dynamic-table.component.css']
})
export class DynamicTableComponent implements OnInit {
  @Input() tableData: any;
  /**
   * tableData add data list from api
   * "tableHeader","actionBtns","actionLinks" and "objectLabel" key name must include as table data.
   * tableHeader array for showing table column.Tableheader name can get from enum "Label".
   * actionBtns array if table includes action column.Action name can get from enum "ActionLabel".
   * objectLabel array for table column filter so name must same from api key name
   * eg.  public tableHeader = [Label.createDate, Label.name, Label.email, Label.phonenumber, Label.action];
          public actionBtns = [ActionLabel.edit, ActionLabel.delete];
          this.userLists.tableHeader = this.tableHeader;
          this.userLists.actionBtns = this.actionBtns;
          this.userLists.objectLabel = ["createdDate", "username", "email", "phoneNumber"];
   */
  @Input() filter: any;

  /**
   *   this.filter={
        sortColumn:this.objectLabel[0],
        sortDirection:"asc",
        index:0
      };
   */
  @Output() onEditModal: any = new EventEmitter<any>();
  @Output() setfilter: any = new EventEmitter<any>();
  public isModal: boolean = false;
  public currentData: any;
  public currentUrl: any;
  public selectedPayment: any;

  constructor(private _router: Router, private _service: SupportService,
    @Inject(DOCUMENT) private document: Document) { }

  ngOnInit(): void {
    this.currentUrl = this._router.url;
  }

  ngOnChanges(): void {
    // this.ansList = [];
  }

  onfilter(index: number): void {
    //  sortColumn:this.objectLabel[0],
    // sortDirection:"asc",
    // index:0
    let filter: any = {};
    filter.sortDirection = index == this.filter.index ? (this.filter.sortDirection == "asc" ? "desc" : "asc") : "asc";
    filter.index = index;
    filter.sortColumn = this.tableData.objectLabel[index];
    this.filter = filter;
    this.setfilter.emit(this.filter);
  }

  onClick(name: String, currentData: any) {
    this.currentData = currentData;
    if (name === ActionLabel.edit)
      this.checkEdit(currentData);
    else if (name == ActionLabel.delete)
      this.isModal = true;
    else if (name == ActionLabel.view)
      this.selectedPayment = currentData;
  }

  checkEdit(currentData) {
    if (this.tableData.btnLink.isModal)
      this.onEditModal.emit(currentData);
    else
      this._router.navigateByUrl(this.tableData.btnLink.editLink + "/" + currentData[this.tableData.btnLink.idKeyname]);
  }
  onDelete() {
    this.isModal = false;
    this._service.onDelete(this.currentData[this.tableData.btnLink.idKeyname], this.tableData.btnLink.deleteLink)
      .subscribe(() => {
        // this._router.navigateByUrl(this.currentUrl);
        this.document.location.reload();
        // console.log("res")
        // this._router.navigateByUrl(this.currentUrl, { skipLocationChange: true });
      });
  }

  closeDelete() {
    this.isModal = false;
  }

  closeModal(){
    this.selectedPayment=null;
  }

  copyText(text:any) {
    const elem = document.createElement('textarea');
    elem.value = text;
    document.body.appendChild(elem);
    elem.select();
    document.execCommand('copy');
    document.body.removeChild(elem);
  }

}
