import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute,Router } from '@angular/router';

import { SupportService } from "@core/services/support.service";
import { AuthenticationService } from "@core/authentication/authentication.service";
import { IconStatus } from "@enum/app_enum";
@Component({
  selector: 'app-route-direction',
  templateUrl: './route-direction.component.html',
  styleUrls: ['./route-direction.component.css']
})
export class RouteDirectionComponent implements OnInit {
  @Input() dirList:any={};
  /**
   * dirList must be string array.String array must be route name.
   * This name can get from sidemenu and common enum.("AdminSideMenu","CommonExpands","TeacherSideMenu")
   */
  public icon:String;
  public selectedRouteRole:String;

  constructor(private _ser:SupportService,private _route:Router,private _auth:AuthenticationService) { }

  ngOnInit(): void {
    this.icon=this._ser.getIcon(this.dirList.routeName[0],IconStatus.fill);
    this.selectedRouteRole=this._auth.currentUserValue.userType.toLocaleLowerCase();
  }

  goRoute(index:number){
    if(this.dirList.routeLink[index]){
      this._route.navigateByUrl(this.dirList.routeLink[index]);
    }
  }
}
