import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-tabs',
  templateUrl: './tabs.component.html',
  styleUrls: ['./tabs.component.css']
})
export class TabsComponent implements OnInit {
  @Input() tabLists: any = [];
  // show tab depend on lists.Write tab name 
  //tab name can from enum.So we can get unique name

  @Output() tabChange: any = new EventEmitter<any>();
  //tab change give index.We can find active index from tabLists array.
  //So we can check and know active tab and then we can do their process depend on active tab.

  public activeIndex: any = 0;

  constructor() { }

  ngOnInit(): void {
    console.log(this.tabLists);
    this.tabChange.emit(0);
  }

  tagChange(index: any) {
    this.tabChange.emit(index);
    this.activeIndex = index;

  }

}
