import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-pagination',
  templateUrl: './pagination.component.html',
  styleUrls: ['./pagination.component.css']
})
export class PaginationComponent implements OnInit {
  @Input() numberPerPage: any;
  @Input() numberOfPages: any;
  @Input() totalCount: any;
  @Output() pageChange: any = new EventEmitter<any>();

  public currentPage: any = 1;
  public pageLists: any;
  public numOfPage:any;
  constructor() { }

  ngOnInit(): void {
    // this.totalCount = 200;
    this.numOfPage = this.getNumberOfPages();
    this.pageLists = this.getPagingRange(this.currentPage, { min: 1, total: this.numOfPage });
  }

  getNumberOfPages() {

    return Math.ceil(this.totalCount / this.numberPerPage);
  }

  getPageLists() {
    return [...Array(this.numOfPage)].map((_, i) => i + 1);
  }

  next(isAllow: boolean) {
    if (isAllow) {
      this.currentPage += 1;
      this.pageChanges();
    }
  }

  prev(isAllow: boolean) {
    if (isAllow) {
      this.currentPage -= 1;
      this.pageChanges();
    }
  }

  firstPage(isAllow: boolean) {
    if (isAllow) {
      this.currentPage = 1;
      this.pageChanges();

    }
  }

  lastPage(isAllow: boolean) {
    if (isAllow) {
      this.currentPage = this.numOfPage;
      this.pageChanges();


    }
  }
  selectedPage(page: any) {
    this.currentPage = page;

    this.pageChanges();
  }

  pageChanges() {
    this.pageChange.emit(this.currentPage);
    this.pageLists = this.getPagingRange(this.currentPage, { min: 1, total: this.numOfPage });
  }
  getPagingRange(current, { min = 1, total = 20, length = 5 } = {}) {
    if (length > total) length = total;

    let start = current - Math.floor(length / 2);
    start = Math.max(start, min);
    start = Math.min(start, min + total - length);
    return Array.from({ length: length }, (el, i) => start + i);
  }
}
