  @Output() onClick: any = new EventEmitter<any>();
    /**
   * medium => btn-medium
   * large=> btn-lg
   * 
   * right side names are class name.
   */
  constructor(private _supportService: SupportService) { }

  ngOnInit(): void {
  }

